.gallery-item__wrapper {
  -ms-flex: none;
      flex: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: solid 1px transparent;
  text-align: center;
  position: relative;
}

.gallery-item__wrapper:hover > .gallery-item__title,
.gallery-item__wrapper:hover > .gallery-item__options {
  display: block;
  background: rgba(255,255,255, .4);
}

.gallery-item__image {
  max-width: 100%;
  max-height: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: block;
  margin: 0 auto;
}

.gallery-item__title {
  position: absolute;
  z-index: 1;
  display: none;
  bottom: 0;
  left: 0;
  right: 1.5em;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.gallery-item__options {
  position: absolute;
  z-index: 1;
  display: none;
  top: 0;
  right: 0;
}

.gallery-item__remove,
.gallery-item__hide,
.gallery-item__open {
  padding: .25em;
  cursor: pointer;
  text-decoration: none;
  color: #000;
}

.gallery-item__remove {
  display: none !important; /* TODO */
}



